import hiatusLogo from '../../static/backgrounds/hiatus/logo.png'
import backLogo from '../../static/backgrounds/back/logo.svg'
import HeaderItem from '../HeaderItem/HeaderItem'
import "./headerStyle.css"

const Header = ({onHiatus}: Props) => {
	return (
		<div id="header">
			<img id="logo" src={onHiatus ? hiatusLogo: backLogo}></img>
			<div id="items">
				<HeaderItem className={onHiatus ? "ItemHiatus" : "ItemPublished"}  text="Hiatus Table"/>
				<HeaderItem className={onHiatus ? "ItemHiatus" : "ItemPublished"}  text="Author Comments" />
				<HeaderItem className={onHiatus ? "ItemHiatus" : "ItemPublished"}  text="About" />
			</div>

		</div>
	)
}

interface Props {
	onHiatus: Boolean|undefined
}

export default Header