import { useEffect } from "react";
import seriesStatusInterface from "../helpers/interfaces/seriesStatusInterface";
import { getSeriesStatus } from "../services/api";
function useStatusAPI(setStatus: React.Dispatch<React.SetStateAction<seriesStatusInterface | undefined>>, setLoading: React.Dispatch<React.SetStateAction<Boolean>>){
	useEffect(() => {
		async function fetchStatus(){
			setLoading(true)
			setTimeout(async () => {
			setStatus(await getSeriesStatus())
			setLoading(false)

			}, 2000)
		}
		fetchStatus()
	}, [setStatus, setLoading])
}

export default useStatusAPI