import React, { Suspense, useState } from 'react';
import './App.css';
import Home from './components/Home/index'
import Loading from './components/Loading/Loading';
import seriesStatusInterface from './helpers/interfaces/seriesStatusInterface';
import useStatusAPI from './hooks/useStatusAPI';

function App() {
	const [status, setStatus] = useState<seriesStatusInterface>()
	const [loading, setLoading] = useState<Boolean>(true)
	useStatusAPI(setStatus, setLoading)
	if (loading)
		return (
			<Loading />
		)
	else {
		return (
			<div className="App">
				<Home status={status} />
			</div>
		)
	}

}

export default App;
