/* eslint-disable jsx-a11y/alt-text */
import loading from '../../static/images/loading.gif'

import '../../App.css'
const Loading = () => {
	return (
		<div id="loading" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: "100vw", height: '100vh' }}>
			<img src={loading}></img>
		</div>
	)
}


export default Loading