import { useEffect } from "react"
import seriesStatusInterface from "../../helpers/interfaces/seriesStatusInterface"
import HomeBody from "../Body/Body"
import Header from "../Header"
import './homeStyle.css'
import '../../backgroundStyles/hiatusBackgroundStyle.css'

const Home= ({status}: Props)=> {
	useEffect(() => {
		if(status?.onHiatus)
			document.body.classList.add('BodyHiatus')
		else{
			document.body.classList.add('BodyPublished')
		}
	})
	return (
		<div className={status?.onHiatus ? "Home HomeHiatus": "Home HomePublished"}>
			<Header onHiatus={status?.onHiatus} />
			<HomeBody status={status} />
		</div>
	)
}

interface Props {
	status: seriesStatusInterface | undefined
}


export default Home